import {
  GET_ITINERARY_DATA_START,
  GET_ITINERARY_DATA_FAIL,
  GET_ITINERARY_DATA_SUCCESS
} from '../actions/actionConstants'

const initialItineraryDataState = {
  itinerary: null,
  error: null,
  isLoading: true
}

const getItineraryDatareducer = (state = initialItineraryDataState, action) => {
  switch (action.type) {
    case GET_ITINERARY_DATA_START:
      return {
        ...state,
        isLoading: true
      }

    case GET_ITINERARY_DATA_SUCCESS:
      return {
        ...state,
        itinerary: action.payload.data.itinerary,
        isLoading: false
      }

    case GET_ITINERARY_DATA_FAIL:
      return {
        ...state,
        isLoading: false,
        error: action.payload.error
      }

    default:
      return state
  }
}

export default getItineraryDatareducer
