import { combineReducers } from 'redux'
import getTripCountReducer from './getTripCountReducer'
import changeCreateTripDataReducer from './changeCreateTripDataReducer'
import getItineraryDatareducer from './getItineraryDataReducer'

export default combineReducers({
  inititalTripCountDetail: getTripCountReducer,
  createTripData: changeCreateTripDataReducer,
  itineraryData: getItineraryDatareducer
})
